import React from 'react';
import { useEffect, useState } from 'react'
import * as api from '../../api/api'
import service from '../../api/request'
import { Cell, Button, Input, Form, Picker, Field, Uploader, Selector, Notify } from 'react-vant'
import cover from './cover.jpg'
import './style.css'
import error1 from './Vector.svg'
import { useNavigate } from 'react-router-dom'


function Profile() {
    const DEMO_UPLOAD_API = '/file/common/upload'
    const history = useNavigate()
    const upload = async (file: File) => {
        try {
            const body = new FormData()
            body.append('file', file)
            const resp: any = await fetch(DEMO_UPLOAD_API, {
                method: 'POST',
                body,
            })
            const json = await resp.json()
            console.log(json)
            // return url  : {url:'https://img.yzcdn.cn/vant/sand.jpg'}
            if (json.data) {
                // console.log(form.getFieldValue('appendix'))
                form.setFieldValue('appendix', json.data)
                console.log(form.getFieldValue('appendix'))
                return { url: json.data }
            } else {
                return { url: error1 }

            }
        } catch (error) {
            console.log(file.name, error)
            return { url: error1 }
        }
    }
    const [form] = Form.useForm()
    const columns = [
        { text: '', value: 0 },
        { text: '', value: 1 },
        { text: '', value: 2 },
        { text: '', value: 3 },
        { text: '', value: 4 },
        { text: '', value: 5 },
        { text: '', value: 6 },
        { text: '', value: 7 },
        { text: '', value: 8 },
    ]
    const [value, setValue] = useState('')

    const [profile, setProfile] = useState({
        email: '',
        schoolId: '',
    });

    const onFinish = (values: any) => {
        console.log(values)
    }

    useEffect(() => {
        getProfile();
    }, []);
    const getProfile = async () => {
        const token = window.localStorage.getItem("token")
        service.defaults.headers.common = { 'Authorization': `bearer ${token}` }
        try {
            const res: any = await api.profile()
            setProfile(res)
            if (res.schoolId == '') {
                history('/login')
            }
            if (res.status != -1) {
                history('/status')
            }
        } catch (error) {
            history('/login')
            console.log(error, 'error')
        }

    }
    const submit = async (values: any) => {
        if (values.appendix === undefined) {
            values.appendix = []
        }
        try {
            let res: any
            console.log(values)
            if (values.appendix.length > 0) {
                res = await api.submit({
                    ...values,
                    schoolId: profile.schoolId,
                    gender: 1,
                    appendix: JSON.stringify(values.appendix[0].url)
                })
            } else {
                res = await api.submit({
                    ...values,
                    schoolId: profile.schoolId,
                    gender: 1,
                    appendix: ''
                })
            }
            if (res.name) {
                window.location.href = './status'
            }
        } catch (error) {
            Notify.show('')
            console.log(error, 'submit error')
        }
    }
    return (
        <div className='me-container'>
            <div><img className='cover' src={cover} alt="" /></div>
            <Cell.Group>
                <Cell title='' value={profile.schoolId} />
            </Cell.Group>
            <Form
                form={form}
                onFinish={submit}
                footer={
                    <div style={{ margin: '16px 16px 0' }}>
                        <Button round nativeType='submit' type='primary' block>
                            
                        </Button>
                    </div>
                }
            >
                {/* <Form.Item
                    tooltip={{
                        message:
                            'A prime is a natural number greater than 1 that has no positive divisors other than 1 and itself.',
                    }}
                    intro=''
                    rules={[{ required: true, message: '' }]}
                    name='username'
                    label=''
                >
                    <Input placeholder='' />
                </Form.Item> */}
                <Form.Item
                    rules={[{ required: true, message: '' }]}
                    name='name'
                    label=''
                >
                    <Input placeholder='' />
                </Form.Item>
                <Form.Item
                    rules={[{
                        required: true,
                        validator: (_, value) => {
                            if (/1\d{10}/.test(value)) {
                                return Promise.resolve(true)
                            }
                            return Promise.reject(new Error(''))
                        },
                    }]}
                    name='phone'

                    label=''
                >
                    <Input placeholder='' />
                </Form.Item>
                <Form.Item
                    rules={[{
                        required: true, validator: (_, value) => {
                            if (/^[A-Za-z0-9]+([_\.][A-Za-z0-9]+)*@([A-Za-z0-9\-]+\.)+[A-Za-z]{2,6}$/.test(value)) {
                                return Promise.resolve(true)
                            }
                            return Promise.reject(new Error(''))
                        },
                    }]}
                    name='email'
                    label=''
                >
                    <Input placeholder='' />
                </Form.Item>
                <Form.Item
                    isLink
                    rules={[{ required: true, message: '' }]}
                    name='skill'
                    label=''
                    trigger='onConfirm'
                    onClick={(_, action: any) => {
                        action.current?.open()
                    }}
                >
                    <Picker
                        popup
                        columns={[
                            '/',
                            '',
                            '',
                            '',
                        ]}
                    >
                        {val => val || ''}
                    </Picker>
                </Form.Item>
                <Form.Item
                    rules={[{ required: false, message: '' }]}
                    name='skillAddition'
                    label=''
                >
                    <Input.TextArea
                        placeholder="..."
                        autoSize={{ minHeight: 80, maxHeight: 120 }}
                    />
                </Form.Item>

                <Form.Item rules={[{ required: true, message: '' }]} name='opt1' label=''>
                    <Selector
                        options={[
                            {
                                label: '',
                                description: '/',
                                value: '1',
                            },
                            {
                                label: '',
                                description: '/',
                                value: '2',
                            },
                            {
                                label: '',
                                description: 'UI/UX/',
                                value: '3',
                            },
                        ]}
                    />
                </Form.Item>
                <Form.Item rules={[{ required: false, message: '' }]} name='opt2' label=''>
                    <Selector
                        options={[
                            {
                                label: '',
                                description: '/',
                                value: '1',
                            },
                            {
                                label: '',
                                description: '/',
                                value: '2',
                            },
                            {
                                label: '',
                                description: 'UI/UX/',
                                value: '3',
                            },
                        ]}
                    />
                </Form.Item>
                <Form.Item
                    rules={[{ required: true, message: '' }]}
                    name='introduce'
                    label=''
                >
                    <Input.TextArea
                        placeholder=""
                        autoSize={{ minHeight: 80, maxHeight: 120 }}
                    />
                </Form.Item>
                <Form.Item
                    rules={[{ required: true, message: '' }]}
                    name='joinReason'
                    label=''
                >
                    <Input.TextArea
                        placeholder=""
                        autoSize={{ minHeight: 80, maxHeight: 120 }}
                    />
                </Form.Item>
                <Form.Item
                    name='appendix'
                    label=''
                    intro=''
                    rules={[{ required: false, message: '' }]}
                >
                    <Uploader
                        maxCount={1}
                        upload={upload}
                        defaultValue={[]}
                        previewCoverRender={(
                            item // 
                        ) =>
                            item.url && (
                                <div
                                    style={{
                                        position: 'relative',
                                        width: ' 100%',
                                        height: '100%',
                                        color: '#fff',
                                        fontSize: 12,
                                        textAlign: 'center',
                                        lineHeight: '650%',
                                        background: '#111111',
                                    }}
                                >
                                    
                                </div>
                            )
                        }
                    />
                </Form.Item>
            </Form>
        </div>
    )
}

export default Profile